<template>
  <div>
    <form role="form" @submit.prevent="onSubmit">
      <div class="card card-success card-outline">
        <div class="card-body">
          <div class="form-group">
            <label for="greenSpace_name">Name</label>
            <input
              type="text"
              class="form-control"
              v-model="greenSpace_name"
              id="greenSpace_name"
              placeholder="Enter greenSpace's Name"
              autocomplete="off"
            />
            <span class="error invalid-feedback" data-error=""></span>
          </div>
          <div class="form-group">
            <label>Establishment Type</label>
            <select class="form-control" v-model="greenSpace_establishmentType">
              <option
                v-for="(establishmentType, index) in establishmentTypes"
                :index="index"
                v-bind:key="establishmentType.id"
                v-bind:value="establishmentType.id"
              >
                {{ establishmentType.libel }}
              </option>
            </select>
          </div>
          <button
            type="button"
            class="btn btn-block btn-success"
            data-toggle="modal"
            data-target="#addMapMaker"
          >
            <i class="fas fa-map-marker-alt"></i>
          </button>
        </div>
        <div class="card-footer">
          <button type="button" class="btn btn-default" @click="clearFields()">
            Cancel
          </button>
          <button type="submit" class="btn btn-success float-right">
            Save
          </button>
        </div>
        <!-- /.card-body -->
      </div>
      <div class="modal fade" id="addMapMaker">
        <!-- /.modal-dialog -->
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-body">
              <div id="map-form"></div>
            </div>
            <div class="modal-footer justify-content-between">
              <button
                type="button"
                class="btn btn-default"
                data-dismiss="modal"
              >
                Cancel
              </button>
              <button
                type="button"
                data-dismiss="modal"
                class="btn btn-success"
              >
                Save
              </button>
            </div>
          </div>
          <!-- /.modal-content -->
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import GreenSpaceService from "./greenSpaceService";
import AlertService from "../alertService";
import MapService from "../maploader";
const greenSpaceService = new GreenSpaceService();
const alertService = new AlertService();
const mapservice = new MapService();
export default {
  name: "GreenSpaceForm",
  props: {
    editinggreenSpace: Object,
    establishmentTypes: Array,
  },
  data() {
    return {
      greenSpace_id: null,
      greenSpace_name: "",
      greenSpace_establishmentType: 0,
      greenSpace_positionLtd: 0.0,
      greenSpace_positionLgt: 0.0,
      map_form: null,
      map_marker: null,
      messageError: [],
    };
  },
  mounted() {
    mapservice
      .getLoader()
      .load()
      .then(() => {
        this.setMapCenter();
        this.setMarker(new google.maps.LatLng(0.0, 0.0));
      });
  },
  methods: {
    onSubmit: function () {
      const greenSpace = {
        id: this.greenSpace_id,
        name: this.greenSpace_name,
        positionLtd: this.greenSpace_positionLtd,
        positionLgt: this.greenSpace_positionLgt,
        establishmentType: this.greenSpace_establishmentType,
      };

      greenSpaceService
        .writeGreenSpace(greenSpace)
        .then((res) => {
          if (greenSpace.id == null) {
            if (res.data.data.createGreenspace.result == true) {
              this.clearFields();
              alertService.showToast(
                res.data.data.createGreenspace.message,
                "success"
              );
              this.$emit(
                "greenSpaceCreated",
                res.data.data.createGreenspace.greenSpace
              );
            } else {
              alertService.showToast(
                res.data.data.createGreenspace.message,
                "error"
              );
            }
          } else {
            if (res.data.data.updateGreenspace.result == true) {
              this.clearFields();
              alertService.showToast(
                res.data.data.updateGreenspace.message,
                "success"
              );
              this.$emit(
                "greenSpaceCreated",
                res.data.data.updateGreenspace.greenSpace
              );
            } else {
              alertService.showToast(
                res.data.data.updateGreenspace.message,
                "error"
              );
            }
          }
        })
        .catch((err) => {
          console.log(err);
          /*let i;
          for (i = 0; i < err.response.data["violations"].length; i++) {
            this.messageError.push(err.response.data["violations"][i].message);
          }
          $(".toast").toast("show");*/
        });
    },
    clearFields: function () {
      this.greenSpace_id = null;
      this.greenSpace_name = "";
      this.greenSpace_establishmentType = 0;
      this.greenSpace_positionLtd = 0.0;
      this.greenSpace_positionLgt = 0.0;
      this.map_marker.setMap(null);
      this.setMapCenter();
      this.messageError = [];
    },
    setGreenSpacePosition: function (position) {
      this.map_marker.setMap(null);
      this.greenSpace_positionLtd = position.coords.toJSON().lat;
      this.greenSpace_positionLgt = position.coords.toJSON().lng;
      this.setMarker(position.coords);
    },
    setMapCenter: function () {
      this.map_form = new google.maps.Map(document.getElementById("map-form"), {
        center: {
          lat: this.greenSpace_positionLtd,
          lng: this.greenSpace_positionLgt,
        },
        zoom: 2,
      });
      this.map_form.addListener("click", (event) => {
        this.setGreenSpacePosition({ coords: event.latLng });
      });
    },
    setMarker: function (position) {
      this.map_marker = new google.maps.Marker({
        position: position,
        map: this.map_form,
        icon: "https://developers.google.com/maps/documentation/javascript/examples/full/images/beachflag.png",
      });
    },
  },
  watch: {
    editinggreenSpace(greenSpace) {
      this.greenSpace_id = greenSpace.id;
      this.greenSpace_name = greenSpace.name;
      this.greenSpace_positionLtd = greenSpace.positionLtd;
      this.greenSpace_positionLgt = greenSpace.positionLgt;
      this.greenSpace_establishmentType = greenSpace.establishmentType.id;
      this.map_marker.setMap(null);
      this.setMapCenter();
      this.setMarker(
        new google.maps.LatLng(greenSpace.positionLtd, greenSpace.positionLgt)
      );
    },
  },
};
</script>
<style scoped>
#map-form {
  width: 100%;
  height: 400px;
}
</style>