import axios from 'axios'
import AxiosConfig from '../axiosConfig.js'

const axiosConfig = new AxiosConfig();
export default class GreenSpaceService {
    async getGreenSpaces() {
        const data = await axios({
            method: "POST",
            url: axiosConfig.getbaseURL(),
            data: {
                query: `{ 
                    greenSpaces{
                        id
                        name
                        positionLtd
                        positionLgt
                        establishmentType{
                            id
                            libel
                        }
                    }
                }`
            }
        });
        return data
    }

    async getEstablishmentTypes() {
        const data = await axios({
            method: "POST",
            url: axiosConfig.getbaseURL(),
            data: {
                query: `{ 
                    establishmentTypes{
                        id
                        libel
                    }
                }`
            }
        });
        return data
    }

    async writeGreenSpace(greenSpace) {
        if (greenSpace.id == null) {
            const data = await axios.post(axiosConfig.getbaseURL(),
                {
                    query: `
                    mutation ($name: String!, $establishmentTypeId: Int!, $positionLtd: Decimal!, $positionLgt: Decimal!) {
                        createGreenspace (
                            name: $name,
                            establishmentTypeId: $establishmentTypeId,
                            positionLtd: $positionLtd,
                            positionLgt: $positionLgt
                        ){
                            greenSpace {
                                id
                                name
                                positionLtd
                                positionLgt
                                establishmentType{
                                    id
                                    libel
                                }
                            }
                            message
                            result
                        }
                    }`,
                    variables: {
                        name: greenSpace.name,
                        establishmentTypeId: greenSpace.establishmentType,
                        positionLtd: greenSpace.positionLtd,
                        positionLgt: greenSpace.positionLgt
                    }
                },
                {
                    headers: axiosConfig.getheaders()
                });
            return data;
        } else {
            const data = await axios.post(axiosConfig.getbaseURL(),
                {
                    query: `
                    mutation ($greenSpaceId: Int!, $name: String!, $establishmentTypeId: Int!, $positionLtd: Decimal!, $positionLgt: Decimal!) {
                        updateGreenspace (
                            greenSpaceId: $greenSpaceId,
                            name: $name,
                            establishmentTypeId: $establishmentTypeId,
                            positionLtd: $positionLtd,
                            positionLgt: $positionLgt
                        ){
                            greenSpace {
                                id
                                name
                                positionLtd
                                positionLgt
                                establishmentType{
                                    id
                                    libel
                                }
                            }
                            message
                            result
                        }
                    }`,
                    variables: {
                        greenSpaceId: greenSpace.id,
                        name: greenSpace.name,
                        establishmentTypeId: greenSpace.establishmentType,
                        positionLtd: greenSpace.positionLtd,
                        positionLgt: greenSpace.positionLgt
                    }
                },
                {
                    headers: axiosConfig.getheaders()
                });
            return data;
        }
    }

    async deleteGreenSpace(greenSpace) {
        const body = {
            query: `
                mutation($greenSpaceId:Int!){
                    deleteGreenspace(
                        greenSpaceId: $greenSpaceId
                    ){
                        message
                        result
                    }
                }`,
            variables: {
                greenSpaceId: greenSpace.id
            }
        }

        const data = await axios.post(axiosConfig.getbaseURL(), body, {
            headers: axiosConfig.getheaders()
        });
        return data
    }
}