<template>
  <div>
    <div class="container-fluid">
      <div class="row">
        <div class="col-6">
          <div class="card card-success card-outline">
            <div class="card-header">
              <h3 class="card-title">Green Areas</h3>
            </div>
            <!-- /.card-header -->
            <div class="card-body table-responsive p-0">
              <table class="table table-head-fixed text-nowrap">
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Name</th>
                    <th>Establishment Type</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(greenSpace, index) in greenSpaces"
                    v-bind:item="greenSpace"
                    :index="index"
                    :key="greenSpace.id"
                  >
                    <td>{{ index + 1 }}</td>
                    <td>{{ greenSpace.name }}</td>
                    <td>{{ greenSpace.establishmentType.libel }}</td>
                    <td>
                      <i
                        class="fas fa-edit"
                        style="cursor: pointer"
                        @click="editGreenSpace(greenSpace)"
                      ></i>
                      ¦
                      <i
                        class="fas fa-trash"
                        style="cursor: pointer"
                        @click="deleteGreenSpace(greenSpace)"
                      ></i>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <!-- /.card-body -->
          </div>
        </div>
        <div class="col-6">
          <GreenSpaceForm
            @greenSpaceCreated="addGreenSpace"
            :editinggreenSpace="editinggreenSpace"
            :establishmentTypes="establishmentTypes"
          />
        </div>
      </div>
      <div class="row">
        <div id="map"></div>
      </div>
    </div>
  </div>
</template>
<script>
import Swal from "sweetalert2";
import GreenSpaceService from "./greenSpaceService";
import AlertService from "../alertService";
import GreenSpaceForm from "./GreenSpaceForm.vue";
import MapService from "../maploader";

const greenSpaceService = new GreenSpaceService();
const alertService = new AlertService();
const mapservice = new MapService();
export default {
  name: "GreenSpace",
  components: { GreenSpaceForm },
  props: {},
  data() {
    return {
      greenSpaces: [],
      establishmentTypes: [],
      editinggreenSpace: null,
      idcheckbox: null,
      map_markers: {},
      map: null,
    };
  },
  mounted() {
    greenSpaceService
      .getGreenSpaces()
      .then((res) => {
        this.greenSpaces = res.data.data.greenSpaces;
        mapservice
          .getLoader()
          .load()
          .then(() => {
            if (res.data.data.greenSpaces.length > 0) {
              this.setMapCenter(
                this.greenSpaces[0].positionLtd,
                this.greenSpaces[0].positionLgt
              );
              this.greenSpaces.forEach(this.initMarkers);
            } else {
              this.setMapCenter(0.0, 0.0);
            }
          });
      })
      .catch((err) => console.error(err));

    greenSpaceService
      .getEstablishmentTypes()
      .then((res) => {
        this.establishmentTypes = res.data.data.establishmentTypes;
      })
      .catch((err) => console.error(err));
  },
  methods: {
    addGreenSpace: function (greenSpace) {
      if (this.greenSpaces.find((n) => n.id === greenSpace.id)) {
        const index = this.greenSpaces.findIndex((n) => n.id === greenSpace.id);
        this.greenSpaces.splice(index, 1, greenSpace);
        this.map_markers[greenSpace.id].setMap(null);
        this.setMarker(
          new google.maps.LatLng(
            greenSpace.positionLtd,
            greenSpace.positionLgt
          ),
          greenSpace.id
        );
      } else {
        this.greenSpaces.unshift(greenSpace);
        this.setMarker(
          new google.maps.LatLng(
            greenSpace.positionLtd,
            greenSpace.positionLgt
          ),
          greenSpace.id
        );
      }
    },
    editGreenSpace: function (greenSpace) {
      this.editinggreenSpace = greenSpace;
    },
    deleteGreenSpace: function (greenSpace) {
      Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#28a745",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
      }).then((result) => {
        if (result.isConfirmed) {
          greenSpaceService
            .deleteGreenSpace(greenSpace)
            .then((res) => {
              if (res.data.data.deleteGreenspace.result == true) {
                this.greenSpaces = this.greenSpaces.filter(
                  (n) => n.id !== greenSpace.id
                );
                this.map_markers[greenSpace.id].setMap(null);
                delete this.map_markers[greenSpace.id];
                alertService.showToast(
                  res.data.data.deleteGreenspace.message,
                  "success"
                );
              } else {
                alertService.showToast(
                  res.data.data.deleteGreenspace.message,
                  "error"
                );
              }
            })
            .catch((err) => console.error(err));
        }
      });
    },
    setMapCenter: function (lat, lgt) {
      this.map = new google.maps.Map(document.getElementById("map"), {
        center: {
          lat: lat,
          lng: lgt,
        },
        zoom: 1,
      });
    },
    initMarkers: function (value) {
      this.setMarker(
        new google.maps.LatLng(value.positionLtd, value.positionLgt),
        value.id
      );
    },
    setMarker: function (position, marker_id) {
      var map_marker = new google.maps.Marker({
        position: position,
        map: this.map,
        icon: "https://developers.google.com/maps/documentation/javascript/examples/full/images/beachflag.png",
      });

      map_marker.metadata = { id: marker_id };
      this.map_markers[marker_id] = map_marker;
    },
  },
};
</script>
<style scoped>
#map {
  width: 100%;
  height: 400px;
}
</style>